.card-container {
	height: 200px;
	border-radius: 10px;
	padding: 5px;
	transition: ease-out 0.3s;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	overflow: hidden;

		img {
			height: 100%;
		}


	.card-container:hover{
		transform: translateY(-10px);
	}

	h1 {
		font-size: 5vh;
		height: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	p {	
		word-break: break-all;
		white-space: normal;
		border: 1px solid rgb(207, 0, 0);
		height: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	
}